@import url('../../utils/base.css');

.category-card {
    position: relative;
    width: 90vw;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    background-color: var(--light-grey);
    padding-top: 6rem;
    padding-bottom: 2rem;
    gap: 0.6rem;
    font-weight: 600;
    letter-spacing: 2px;
    border-radius: 5%;
}

.category-card div:first-child img {
    position: absolute;
    transform: translateY(-30%) translateX(-50%);
    top: 0;
    width: 160px;
    bottom: 20px;
}

.category-card button {
    display: flex;
    height: 12px;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    color: var(--dark-grey);
    font-weight: 500;
    outline: 0;
    border: 0;
    background: rgba(0,0,0,0);
}